export default {
	data() {
		return {
			partyAddress: '',
			managerPhone: '',
			manager: '',
			partyTitle: '',
			signUrl: '',
			date: '',
			titleContent: [{
				title: '甲方：',
				content: '成都凡果科技有限公司'
			}, {
				title: '法人：',
				content: '龚果'
			}, {
				title: '地址：',
				content: '成都市郫都区金融中心B栋11楼10号'
			}]
		}
	},
	onLoad() {
		this.shopRequest()
		this.getStorage()
		this.getDate()
	},
	methods: {
		shopRequest(){
			uni.getStorage({
				key: 'openId',
				success:res => {
					this.openId = res.data
					uni.request({
						url: 'https://www.finetwm.com/xmRepair/shopInfo/findIdByOpenid',
						method: 'GET',
						data: {
							openid: this.openId
						},
						success: res => {
							console.log(res.data.data.shopid)
							let id = res.data.data.shopid
							uni.request({
								url: 'https://www.finetwm.com/xmRepair/shopInfo/get',
								method: 'GET',
								data: {
									id: id
								},
								success: res => {
									console.log(res.data.data)
									this.partyTitle = res.data.data.name
									this.manager = res.data.data.manager
									this.managerPhone = res.data.data.phone
									let area = res.data.data.area
									let street = res.data.data.street
									let address = res.data.data.detail_address
									this.partyAddress = area + street + address
								},
								fail: err => {
									console.log(err)
								}
							})
						},
						fail: err => {
							console.log(err)
						}
					})
				}
			})
		},
		getDate(){
			let date = new Date()
			let year = date.getFullYear()
			let month = date.getMonth() + 1
			let day = date.getDate()
			this.date = year + '年' + month + '月' + day + '日'
		},
		onRule() {
			uni.navigateTo({
				url: 'rule'
			})
		},
		onSign() {
			uni.navigateTo({
				url: 'sign'
			})
		},
		requestUrl() {
			uni.uploadFile({
				url: 'https://www.finetwm.com/xmRepair/shopInfo/upload', //仅为示例，非真实的接口地址
				filePath: this.signUrl,
				header: {
					'content-Type': 'multipart/form-data'
				},
				name: 'file',
				formData: {
					type: 4,
					stat: 4,
					openid: this.openId
				},
				success:res => {
					console.log(res)
					uni.showToast({
						title: '提交成功',
					})
					setTimeout(() => {
						uni.navigateTo({
							url: 'setting-key'
						})
					}, 1000)
				}
			})
		},
		onSubmit() {
			if (this.signUrl == '') {
				uni.showToast({
					title: '请签名',
					icon: 'none'
				})
			} else {
				this.requestUrl()
			}
		},
		getStorage() {
			uni.getStorage({
				key: 'signUrl',
				success: res => {
					this.signUrl = res.data
				}
			})
			uni.getStorage({
				key: 'partyName',
				success: res => {
					this.partyTitle = res.data
				}
			})
			uni.getStorage({
				key: 'partyManager',
				success: res => {
					this.manager = res.data
				}
			})
			uni.getStorage({
				key: 'partyPhone',
				success: res => {
					this.managerPhone = res.data
				}
			})
			uni.getStorage({
				key: 'partyAddress',
				success: res => {
					this.partyAddress = res.data
				}
			})
		}
	}
}
